import React, { Component,useState} from 'react';
import Vunhat from './img/a.jpg';
import Nhuthuy from './img/b.jpg';
import Hearts from './img/hearts.svg';

function Info() {
    return (
        <div className="about">
            <div className="info">
                <img src={Vunhat} className="avatar" alt="Vũ Minh Nhật" />
                <div className="details">
                    <p className="name">Vũ Minh Nhật</p>
                    <p className="born">10.09.2003</p>
                </div>
            </div>
                <div className="heart-box">
                    <img src={Hearts} className="heart" id="heart" />
                </div>
            <div className="info">
                <img src={Nhuthuy} className="avatar" alt="Trần Thị Như Thùy" />
                <div className="details">
                    <p className="name">Trần Thị Như Thùy</p>
                    <p className="born">26.01.2006</p>
                </div>
            </div>
        </div>
    );
}
export default Info;


