import React, { Component,useState} from 'react';

function Time() {
    return(
        <div className="time">
            <h2 className="days"><span>00</span>&nbsp;DAYS</h2>
            <p className="sup-time">
                <span className="hour">10</span>:<span className="min">20</span>:<span className="second">16</span>
            </p>
        </div>
    )
}
export default Time;



